.narahosting-hero-block {
	min-height: 33rem;
	display: flex;
	align-items: center;
	background-size: cover;
	background-position: center center;
	padding: 60px 30px;
	margin-bottom: 8rem;
}

.narahosting-hero-content {
	max-width: 600px;
	color: #fff;
	margin-left: 13%;
}

.narahosting-hero-title {
	font-size: 2.5rem;
	margin-bottom: 10px;
	font-weight: bolder;
	color: #fff;
}

.narahosting-hero-subtitle {
	font-size: 1.25rem;
	margin-bottom: 20px;
}

.narahosting-hero-button {
	background: #F87060;
	color: #fff;
	padding: 14px 28px;
	text-decoration: none;
	border-radius: 4px;
	font-weight: bold;
	font-weight: normal;
}